
import Alert from 'vux/src/components/alert/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Swiper from '../../static/swiper/js/swiper.js';
import Vue from 'vue'

Vue.use(AlertPlugin)

export default {
components: {
alert,Msg,Badge
},
data(){
return {
}
},
methods:{
jump(id){
this.$router.push('/');
},
alert() {
let that =this
this.$vux.alert.show({
title: 'Vux is Cool',
content: 'Do you agree?',
onHide () {
console.log('Plugin: I\'m hiding')
that.$router.push('/pz');
}
})

},

},
mounted: function(){
new Swiper('.swiper-container', {
navigation: {
nextEl: '.swiper-button-next',
prevEl: '.swiper-button-prev',
},








pagination: {
el: '.swiper-pagination',
clickable :true,
},
slidesPerView: 3,
slidesPerColumn: 2,
paginationClickable: true,
slidesPerColumnFill : 'column',
spaceBetween: 30
});
}

}




